import {ATextInput} from "./abstract/a-text-input";
import {InputAddon} from "./input-addon";

/**
 * @classdesc Input personalizado para senhas
 * 
 * <img src="../../doc_assets/passwordinput_example.png" />
 * 
 * @example
 * 
 * import {PasswordInput}  from "mvcomponents/input";
 * 
 * let passwordInput:PasswordInput = new PasswordInput("minhasenha");
 * 
 * this.append(passwordInput);
 * 
 * @export
 * @extends {ATextInput}
 * @param {string} [value=null] Altera valor do campo.
 * @memberof module:mvcomponents/input
 */
export class PasswordInput extends ATextInput {
	constructor(value:string=""){
		super("password", value);
		this.addStyleName("InputPassWord InputText");
	}
}
